.search {
  left: 50%;
  top: 5%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 50%;
  margin-top: 5rem;
  text-align: center;
  position: absolute; }
  .search__field {
    width: 30rem;
    border-radius: 4px;
    font-size: 2rem;
    padding-left: 2rem;
    height: 5rem;
    font-family: inherit; }
  .search__btn, .search__btn:link, .search__btn:visited {
